export default {
	name: 'trends-list',
	components: {},
	props: {
		list_data: {
			type: Array,
			default: () => {
				return []
			}
		},
		viewBtn: {
			type: Boolean,
			default: true
		}
	},
	methods: {
		userTap(tap, data, index) {
			this.$emit('userTap', {
				data,
				index
			})
		},
		titleTap(tap, data, index) {
			this.$emit('titleTap', {
				data,
				index
			})
		},
		contentTap(tap, data, index) {
			this.$emit('contentTap', {
				data,
				index
			})
		},
		imgTap(img, arr, index) {
			this.$emit('imgTap', {
				img,
				arr,
				index
			})
		}
	},
	filters:{
		addressFilter(addr){
			return addr
		}
	}
}
